import Latitude from "./Latitude";
import Longitude from "./Longitude";
import Point3D from "./Point3D";
export default class Line3D {
    constructor(points) {
        this._value = points;
    }
    static createFromString(stringLine) {
        const points = [];
        const arrayLine = stringLine
            .trim()
            .replace("\n", "")
            .replace("  ", " ")
            .replace("  ", " ")
            .split(",");
        arrayLine.forEach((stringPoint) => {
            const arrayPoint = stringPoint.trim().split(" ");
            points.push(new Point3D(new Longitude(parseFloat(arrayPoint[0])), new Latitude(parseFloat(arrayPoint[1])), new Latitude(parseFloat(arrayPoint[2]))));
        });
        return new Line3D(points);
    }
    get value() {
        return this._value;
    }
    toString() {
        const points = [];
        this.value.forEach((point) => {
            points.push(point.toString);
        });
        return `[${points.join("],[")}]`;
    }
    toProcessString() {
        const points = [];
        this.value.forEach((point) => {
            points.push(point.toProcessString());
        });
        return `${points.join(",")}`;
    }
    toLineString3D() {
        return '{ "type": "LineString", "coordinates": [' + this.toString() + "] }";
    }
    toFeatureCollection() {
        const features = [];
        for (let index = 1; index < this._value.length; index++) {
            features[index - 1] = `{
        "type": "Feature",
        "geometry": {
          "type": "MultiLineString",
          "coordinates": [
            [
              [ ${this._value[index - 1].toString2D} ], [ ${this._value[index].toString2D} ]
            ]
          ]
        },
        "properties": {
          "index": ${index},
          "initial_height": ${this._value[index - 1].height.value},
          "final_height": ${this._value[index].height.value}
        },
        "id": "${index - 1}"
      }`;
        }
        return `{
      "type": "FeatureCollection",
      "features": [ ${features.join(",")} ]
    }`;
    }
}
