import Latitude from "./Latitude";
import Longitude from "./Longitude";
import Point from "./Point";
export default class Polygon {
    constructor(points) {
        this._value = points;
    }
    static createFromString(stringPolygon) {
        const points = [];
        const arrayPolygon = stringPolygon
            .trim()
            .replace("\n", "")
            .replace("  ", " ")
            .replace("  ", " ")
            .split(",");
        arrayPolygon.forEach((stringPoint) => {
            const arrayPoint = stringPoint.trim().split(" ");
            points.push(new Point(new Longitude(parseFloat(arrayPoint[0])), new Latitude(parseFloat(arrayPoint[1]))));
        });
        return new Polygon(points);
    }
    get value() {
        return this._value;
    }
    toString() {
        const points = [];
        this.value.forEach((point) => {
            points.push(point.toString);
        });
        return `[[${points.join("],[")}]]`;
    }
    coordinates() {
        const jsonString = `{
      "type": "FeatureCollection",
      "name": "test",
      "crs": { "type": "name", "properties": { "name": "urn:ogc:def:crs:OGC:1.3:CRS84" } },
      "features": [
        { "type": "Feature", "properties": { }, "geometry": { "type": "Polygon", "coordinates": [ ${this.toString()} ] } }
      ]
    }`;
        return JSON.parse(jsonString).features[0].geometry.coordinates;
    }
}
