import Latitude from "./Latitude";
import Longitude from "./Longitude";
import Point from "./Point";
export default class Line {
    constructor(points) {
        this._value = points;
    }
    static createFromString(stringLine) {
        const points = [];
        const arrayLine = stringLine
            .trim()
            .replace("\n", "")
            .replace("  ", " ")
            .replace("  ", " ")
            .split(",");
        arrayLine.forEach((stringPoint) => {
            const arrayPoint = stringPoint.trim().split(" ");
            points.push(new Point(new Longitude(parseFloat(arrayPoint[0])), new Latitude(parseFloat(arrayPoint[1]))));
        });
        return new Line(points);
    }
    get value() {
        return this._value;
    }
    toString() {
        const points = [];
        this.value.forEach((point) => {
            points.push(point.toString);
        });
        return `[${points.join("],[")}]`;
    }
    toProcessString() {
        const points = [];
        this.value.forEach((point) => {
            points.push(point.toProcessString());
        });
        return `${points.join(",")}`;
    }
    subLine(maxIndex) {
        const points = [];
        this.value.forEach(function (point, actualIndex) {
            if (actualIndex <= maxIndex) {
                points.push(point.toString);
            }
        });
        return `[${points.join("],[")}]`;
    }
}
