import Latitude from "./Latitude";
import Longitude from "./Longitude";
import Point from "./Point";
export default class PointInLine extends Point {
    constructor(longitude, latitude, positionInLine) {
        super(longitude, latitude);
        this._positionInLine = positionInLine;
    }
    get positionInLine() {
        return this._positionInLine;
    }
    static create(stringPoint, positionInLine) {
        const arrayPoint = stringPoint.split(" ");
        return new PointInLine(new Longitude(parseFloat(arrayPoint[0])), new Latitude(parseFloat(arrayPoint[1])), positionInLine);
    }
}
