import StringValueObject from "../../Shared/domain/StringValueObject";
export default class ContourV2 {
    constructor(polygon, equidistance, wpsEndpoint) {
        this._polygon = polygon;
        this._equidistance = equidistance;
        this._wpsEndpoint = wpsEndpoint;
        this._baseRasterLayer = new StringValueObject("geoprocess:alos_unificado");
    }
    get polygon() {
        return this._polygon;
    }
    get equidistance() {
        return this._equidistance;
    }
    rectangle() {
        return this._polygon.toString();
    }
    get wpsEndpoint() {
        return this._wpsEndpoint;
    }
    get fullWpsEndpoint() {
        return `${this.wpsEndpoint.value}&request=Execute&identifier=gs:Contour`;
    }
    get xmlInput() {
        return `<?xml version="1.0" encoding="UTF-8"?><wps:Execute version="1.0.0" service="WPS" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns="http://www.opengis.net/wps/1.0.0" xmlns:wfs="http://www.opengis.net/wfs" xmlns:wps="http://www.opengis.net/wps/1.0.0" xmlns:ows="http://www.opengis.net/ows/1.1" xmlns:gml="http://www.opengis.net/gml" xmlns:ogc="http://www.opengis.net/ogc" xmlns:wcs="http://www.opengis.net/wcs/1.1.1" xmlns:xlink="http://www.w3.org/1999/xlink" xsi:schemaLocation="http://www.opengis.net/wps/1.0.0 http://schemas.opengis.net/wps/1.0.0/wpsAll.xsd">
    <ows:Identifier>gs:Contour</ows:Identifier>
    <wps:DataInputs>
      <wps:Input>
        <ows:Identifier>data</ows:Identifier>
        <wps:Reference mimeType="image/tiff" xlink:href="http://geoserver/wps" method="POST">
            <wps:Body>
              
              <!-- ras:CropCoverage -->
              <wps:Execute version="1.0.0" service="WPS" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns="http://www.opengis.net/wps/1.0.0" xmlns:wfs="http://www.opengis.net/wfs" xmlns:wps="http://www.opengis.net/wps/1.0.0" xmlns:ows="http://www.opengis.net/ows/1.1" xmlns:gml="http://www.opengis.net/gml" xmlns:ogc="http://www.opengis.net/ogc" xmlns:wcs="http://www.opengis.net/wcs/1.1.1" xmlns:xlink="http://www.w3.org/1999/xlink" xsi:schemaLocation="http://www.opengis.net/wps/1.0.0 http://schemas.opengis.net/wps/1.0.0/wpsAll.xsd">
                <ows:Identifier>ras:CropCoverage</ows:Identifier>
                <wps:DataInputs>
                  <wps:Input>
                    <ows:Identifier>coverage</ows:Identifier>
                    <wps:Reference mimeType="image/tiff" xlink:href="http://geoserver/wcs" method="POST">
                      <wps:Body>
                        <wcs:GetCoverage service="WCS" version="1.1.1">
                          <ows:Identifier>${this._baseRasterLayer.value}</ows:Identifier>
                          <wcs:DomainSubset>
                            <ows:BoundingBox crs="http://www.opengis.net/gml/srs/epsg.xml#4326">
                              <ows:LowerCorner>-74.000000946 -55.666705466</ows:LowerCorner>
                              <ows:UpperCorner>-53.499547847 -21.666460109</ows:UpperCorner>
                            </ows:BoundingBox>
                          </wcs:DomainSubset>
                          <wcs:Output format="image/tiff"/>
                        </wcs:GetCoverage>
                      </wps:Body>
                    </wps:Reference>
                  </wps:Input>
                  <wps:Input>
                    <ows:Identifier>cropShape</ows:Identifier>
                    <wps:Data>
                      <wps:ComplexData mimeType="application/json"><![CDATA[{
                        "geometry": {
                        "type": "Polygon",
                        "coordinates": [ ${this.rectangle()} ] } }]]>
                      </wps:ComplexData>
                    </wps:Data>
                  </wps:Input>
                </wps:DataInputs>
                <wps:ResponseForm>
                  <wps:RawDataOutput mimeType="image/tiff">
                    <ows:Identifier>result</ows:Identifier>
                  </wps:RawDataOutput>
                </wps:ResponseForm>
              </wps:Execute>
              <!-- /ras:CropCoverage -->
  
            </wps:Body>
          </wps:Reference>
      </wps:Input>
      <wps:Input>
        <ows:Identifier>interval</ows:Identifier>
        <wps:Data>
          <wps:LiteralData>${this._equidistance.value}</wps:LiteralData>
        </wps:Data>
      </wps:Input>
    </wps:DataInputs>
    <wps:ResponseForm>
      <wps:RawDataOutput mimeType="application/json">
        <ows:Identifier>result</ows:Identifier>
      </wps:RawDataOutput>
    </wps:ResponseForm>
  </wps:Execute>`;
    }
}
ContourV2.MAX_AREA_ALLOWED = 100000000;
ContourV2.MIN_VALLEY_EQUIDISTANCE_ALLOWED = 10;
ContourV2.MIN_MOUNTAIN_EQUIDISTANCE_ALLOWED = 100;
ContourV2.FIELDS = JSON.parse(`[
      {
        "name": "Capa",
        "element": "select",
        "references": "drawedLayers",
        "allowedTypes": ["rectangle"],
        "points": ["ne", "sw"]
      },
      {
        "name": "Equidistancia",
        "element": "input",
        "type": "number",
        "min": 10,
        "max": 10000
      }
    ]`);
