import Latitude from "./Latitude";
import Longitude from "./Longitude";
export default class Point {
    constructor(longitude, latitude) {
        this._longitude = longitude;
        this._latitude = latitude;
    }
    get value() {
        return [this._longitude, this._latitude];
    }
    get longitude() {
        return this._longitude;
    }
    get latitude() {
        return this._latitude;
    }
    get toString() {
        return `${this._longitude.value},${this._latitude.value}`;
    }
    toProcessString() {
        return `${this._longitude.value} ${this._latitude.value}`;
    }
    static createFromString(stringPoint) {
        const arrayPoint = stringPoint.split(" ");
        return new Point(new Longitude(parseFloat(arrayPoint[0])), new Latitude(parseFloat(arrayPoint[1])));
    }
    get moveLongitude() {
        return new Longitude(this._longitude.value + 0.0000001);
    }
    get moveLatitude() {
        return new Latitude(this._latitude.value + 0.0000001);
    }
}
