export default class MultiPointInLine {
    constructor() {
        this._points = [];
    }
    add(point) {
        this._points.push(point);
        this.reorderPointsByPositionInLine();
    }
    reorderPointsByPositionInLine() {
        this._points.sort((firstPoint, secondPoint) => {
            if (firstPoint.positionInLine < secondPoint.positionInLine) {
                return -1;
            }
            if (firstPoint.positionInLine > secondPoint.positionInLine) {
                return 1;
            }
            return 0;
        });
    }
    get points() {
        return this._points;
    }
    toString() {
        const pointsString = [];
        this._points.forEach((point) => {
            pointsString.push(point.toString);
        });
        return `[${pointsString.join("],[")}]`;
    }
    toJSONFeatureCollection() {
        const pointsString = [];
        this._points.forEach((point, index) => {
            pointsString.push(`{ "type": "Feature", "properties": { "index": ${index} }, "geometry": { "type": "MultiPoint", "coordinates": [ [ ${point.toString}  ] ] } }`);
        });
        return `{
      "type": "FeatureCollection",
      "name": "multipoint",
      "crs": { "type": "name", "properties": { "name": "urn:ogc:def:crs:OGC:1.3:CRS84" } },
      "features": [
        ${pointsString.join(",")}
      ]
    }`;
    }
}
