import NumberValueObject from "./NumberValueObject";
export default class Longitude extends NumberValueObject {
    constructor(longitude) {
        super(longitude);
        this.ensureIsValidLongitude();
    }
    ensureIsValidLongitude() {
        if (this.value < -74 || this.value > -52) {
            throw RangeError("The longitude must be between -74 and -52");
        }
    }
}
