import Height from "./Height";
import Latitude from "./Latitude";
import Longitude from "./Longitude";
export default class Point3D {
    constructor(longitude, latitude, height) {
        this._longitude = longitude;
        this._latitude = latitude;
        this._height = height;
    }
    get value() {
        return [this._longitude, this._latitude, this._height];
    }
    get longitude() {
        return this._longitude;
    }
    get latitude() {
        return this._latitude;
    }
    get height() {
        return this._height;
    }
    get toString() {
        return `${this._longitude.value},${this._latitude.value},${this._height.value}`;
    }
    get toString2D() {
        return `${this._longitude.value},${this._latitude.value}`;
    }
    toProcessString() {
        return `${this._longitude.value} ${this._latitude.value} ${this._height.value}`;
    }
    static createFromString(stringPoint) {
        const arrayPoint = stringPoint.split(" ");
        return new Point3D(new Longitude(parseFloat(arrayPoint[0])), new Latitude(parseFloat(arrayPoint[1])), new Height(parseFloat(arrayPoint[2])));
    }
}
